import React from 'react';
import { connect } from 'react-redux';
import * as encuestaActions from '../../actions/encuestaActions';

class ImagenOpciones extends React.Component{
    state = {
        checked: false,
    };

    handleClick = () => {
        const pregunta = this.props.encuesta.preguntas[this.props.pregunta];

        if (!this.state.checked) {
            if (pregunta.max_respuestas === 1) {
                this.props.agregar_respuesta_uno(this.props.pregunta, this.props.opcion);
                this.setState({
                    checked: true
                });
                return;
            } else {
                this.props.agregar_respuesta(this.props.pregunta, this.props.opcion);
            }
        } else {
            if (pregunta.max_respuestas === 1) {
                this.props.agregar_respuesta_uno(this.props.pregunta, this.props.opcion);
                this.setState({
                    checked: true
                });
                return;
            } else {
                this.props.quitar_respuesta(this.props.pregunta, this.props.opcion);
            } 
        }

        this.setState({
            checked: !this.state.checked
        });
    }

    render() {
        const {opcion} = this.props;
        const pregunta = this.props.encuesta.preguntas[this.props.pregunta];
        let index = -1;
        if (pregunta.max_respuestas === 1) {
            index = pregunta.respuestas.findIndex(res => res.id === this.props.opcion.id);
            console.log('entro');
        }

        let classButton = 'fa fa-check hidden';
        if (this.state.checked && index > -1) {
            classButton += 'fa fa-check checked-image';
        }
        return (   
            <div className="col-xs-4 col-sm-3 col-md-3 text-center image-form">
                <button type="button" onClick={this.handleClick} className="image-check">
                    <img className="img-responsive img-op" src={ opcion.imagen } alt={opcion.imagen}/>
                    <i className={classButton}></i>
                </button>
            </div>
        )
    }
};

const mapStateToProps = (reducers) => reducers.encuestaReducers;

export default connect(mapStateToProps, encuestaActions)(ImagenOpciones);
