import React from 'react';
import axios from 'axios';

class DatosEncuestado extends React.Component {
    state = {
        ciudades: [],
        errores: {}
    }

    async componentDidMount() {
        const respuesta = await axios.get(`https://www.mktestudios.com/api/v1/ciudades/`);
        this.setState({
            ciudades: respuesta.data.results
        });
    }

    handleChange = (ev) => {
        const name = ev.target.id;
        const value = ev.target.value;
        this.setState({
            [name]: value
        });
    }

    validaDatosEncuestado = () => {
        
        const errores = this.props.validaDatosEncuestado();
        if (Object.keys(errores).length) {
            this.setState({
                errores: errores
            });
            return;
        }

        this.props.siguiente();
    }


    render() {
        return (
            <div>
                <div className="row">
                    <div className="col-md-6 form-group">
                        <label htmlFor="nombres">Nombres</label>
                        <input
                            className="form-control" 
                            type="text" 
                            id="nombres" 
                            value={this.props.nombres}
                            onChange={this.props.onChange}
                            required
                        />
                        { this.state.errores['nombres'] ? 
                            <p className="text-muted">{this.state.errores['nombres']}</p>
                            : ''
                        }
                    </div>
                    <div className="col-md-6 form-group">
                        <label htmlFor="apellidos">Apellidos</label>
                        <input 
                            className="form-control" 
                            type="text" id="apellidos" 
                            value={this.props.apellidos} 
                            onChange={this.props.onChange}
                            required
                        />
                        { this.state.errores['apellidos'] ? 
                            <p className="text-muted">{this.state.errores['apellidos']}</p>
                            : ''
                        }
                    </div>
                    <div className="col-md-6 form-group">
                        <label htmlFor="edad">Edad</label>
                        <input 
                            className="form-control" 
                            type="number" 
                            id="edad" 
                            value={this.props.edad} 
                            onChange={this.props.onChange}
                            required
                        />
                        { this.state.errores['edad'] ? 
                            <p className="text-muted">{this.state.errores['edad']}</p>
                            : ''
                        }
                    </div>
                    <div className="col-md-6 form-group">
                        <label htmlFor="genero">Género</label>
                        <select 
                            className="form-control" 
                            type="text" 
                            id="genero" 
                            value={this.props.genero} 
                            onChange={this.props.onChange}
                            required
                        >
                            <option value="H">HOMBRE</option>
                            <option value="M">MUJER</option>
                        </select>
                        { this.state.errores['genero'] ? 
                            <p className="text-muted">{this.state.errores['genero']}</p>
                            : ''
                        }
                    </div>
                    <div className="col-md-6 form-group">
                        <label htmlFor="ciudad">Ciudad</label>
                        <select 
                            className="form-control" 
                            type="text" 
                            id="ciudad" 
                            value={this.props.ciudad} 
                            onChange={this.props.onChange}
                            required
                        >
                            <option>Seleccione uno</option>
                            { 
                                this.state.ciudades.map((ciudad) => (
                                    <option key={ciudad.id} value={ciudad.id}>{ciudad.nombre}</option>
                                ))
                            }
                            
                        </select>
                        { this.state.errores['ciudad'] ? 
                            <p className="text-muted">{this.state.errores['ciudad']}</p>
                            : ''
                        }
                    </div>
                    <div className="col-md-6 form-group">
                        <label htmlFor="email">Correo</label>
                        <input 
                            className="form-control" 
                            type="email" 
                            id="email" 
                            value={this.props.email} 
                            onChange={this.props.onChange}
                            required
                        />
                        { this.state.errores['ciudad'] ? 
                            <p className="text-muted">{this.state.errores['ciudad']}</p>
                            : ''
                        }
                    </div>
                    { this.props.slug === 'infantil' ?
                    <div className="col-md-6 form-check">
                        <label htmlFor="tiene_hijos">Tiene hijos</label>
                        <select 
                            className="form-control" 
                            id="tiene_hijos" 
                            value={this.props.tiene_hijos}
                            onChange={this.props.onChange}
                        >
                            <option value="">Seleccione uno</option>
                            <option value="true">SI</option>
                            <option value="false">NO</option>
                        </select>
                        { this.state.errores['tiene_hijos'] ? 
                            <p className="text-muted">{this.state.errores['tiene_hijos']}</p>
                            : ''
                        }
                    </div> : ''
                    }
                    { this.props.slug === 'infantil' ?
                    <div className="col-md-6 form-group">
                        <label htmlFor="edad">Edad hijos</label>
                        <input 
                            className="form-control" 
                            type="text" 
                            id="edad_hijos" 
                            placeholder="Ejemplo: 12, 2, 3"
                            value={this.props.edad_hijos} 
                            onChange={this.props.onChange}
                        />
                        { this.state.errores['edad_hijos'] ? 
                            <p className="text-muted">{this.state.errores['edad_hijos']}</p>
                            : ''
                        }
                    </div>
                    : ''
                    }
                </div>
                <div className="text-center">
                    { !this.props.mostrarEncuesta ? 
                    <button type="button" className="btn btn-primary" onClick={this.validaDatosEncuestado}>Siguiente</button>
                    : ''
                    }
                </div>
            </div>
        );
    }
}

export default DatosEncuestado;

