import React from 'react';
import { connect } from 'react-redux';
import * as encuestaActions from '../../actions/encuestaActions';
import Loader from '../utils/Loader';
import DatosEncuesta from '../DatosEncuestado';
import Guardado from './Guardado';

import Opciones from '../Encuestas/Opciones'
class Encuesta extends React.Component {
    state = {
        nombres: '',
        apellidos: '',
        edad: '',
        genero: 'M',
        ciudad: '',
        email: '',
        tiene_hijos: false,
        edad_hijos: ''
    }

    async componentDidMount() {
        await this.props.traer_uno(this.props.match.params.slug);
    }

    validaDatosEncuestado = () => {
        const errores = {};
        if (this.state.nombres === '') {
            errores['nombres'] = 'Nombre es requerido';
        }
        if (this.state.apellidos === '') {
            errores['apellidos'] = 'Apellido es requerido';
        }
        if (this.state.edad === 0 || this.state.edad === '') {
            errores['edad'] = 'Edad es requerida';
        }
        if (this.state.ciudad === '') {
            errores['ciudad'] = 'Ciudad es requerido';
        }

        if (this.props.match.params.slug === 'infantil') {
            if (this.state.edad_hijos === '' && this.state.tiene_hijos !== false ) {
                errores['edad_hijos'] = 'La edad de los hijos es requerido';
            }
        }

        if (this.props.match.params.slug === 'infantil') {
            if (this.state.tiene_hijos === false && Object.keys(errores).length === 0) {
                this.props.guardar_datos(this.state);
            }
        }

        return errores;
    }

    handleSumit = (ev) => {
        ev.preventDefault();
        this.props.guardar_datos(this.state);
    }

    handleChangeInput = (ev) => {
        const name = ev.target.id;
        const value = ev.target.value;
        this.setState({
            [name]: value
        });
    }

    mostrarDatosForm = () => {
        console.log(this.props.mostrarEncuesta)
        if (!this.props.mostrarEncuesta) {
            return ;
        }

        return (
            <div>
                {
                    this.props.encuesta.preguntas.map((pregunta, index) => (
                        <div key={pregunta.id} className="content-preguntas">
                            <h5>{ pregunta.nombre }</h5>
                            <p className="text-muted">{ pregunta.descripcion }</p>
                            <Opciones pregunta={index} genero={this.state.genero}/>
                        </div>
                    ))
                }
                <div className="text-center">
                    <button type="submit" className="btn btn-primary">Enviar información</button>
                </div>
            </div>
        )
    }

    render() {
        console.log(this.props);
        if (this.props.cargando) {
            return <Loader />
        }

        if (this.props.mensaje) {
            return <Guardado mensaje={this.props.mensaje} />
        }

        return (
            <form className="form-encuesta" onSubmit={this.handleSumit}>
                <h4 className="text-center">{this.props.encuesta.nombre}</h4>
                <br />
                <DatosEncuesta 
                    mostrarEncuesta={this.props.mostrarEncuesta} 
                    validaDatosEncuestado={this.validaDatosEncuestado} 
                    validaDatosEncuestado={this.validaDatosEncuestado} 
                    siguiente={this.props.siguiente}  
                    slug={this.props.match.params.slug}  
                    onChange={this.handleChangeInput} 
                    {...this.state}
                />
                <br />
                { this.mostrarDatosForm() }
            </form>
        )
    }
}

const mapStateToProps = (reducers) => reducers.encuestaReducers;

export default connect(mapStateToProps, encuestaActions)(Encuesta);
